Работа на уроке с опорными конспектами включает  3 этапа. На первом этапе учитель расшифровывает план работы и план урока . Ученики внимательно слушают. На втором этапе ученики опорные слова и термины переносят себе в рабочую тетрадь. После этого ученик самостоятельно решает поставленные задания исходя из требования учителя. На следующем этапе  учащиеся расшифровывают, просматривают и решают тесты что есть в этом конспекте через QR код, совместно изображенного вуведеного на доску или экран. На третьем этапе ученики вносят дополнение, закрепляют знания  в конспекте при раскрытии темы. Краткие выступления учащихся  по содержанию опорного конспекта у доски с использование плана урока, то есть по памяти проговария основные термины и исторические процессы. Весь класс смотрит на экран, контролируя правильность устных ответов. Далее учащиеся получают задания и   доработывают их дома.
